.zero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: stretch;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.background {
    background-image: url("../../assets/images/LiveMatch.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
}

.float-text {
    color: #fff;
    margin: auto;
    height: auto;
    font-size: 15px;
    line-height: 15px;
    width: 100%;
}

.float{
    position:fixed;
    width:120px;
    height:50px;
    bottom:30px;
    right:30px;
    background-color:#00A298;
    color:#FFF;
    border-radius:50px;
    text-align:center;
    box-shadow: 2px 2px 3px #999;
    z-index: 999;
    display: flex;
    cursor: pointer;
}

.float-whatsapp{
    position:fixed;
    width:60px;
    height:60px;
    bottom:100px;
    right:60px;
    z-index: 999;
    background: #ff;
    border-radius:50px;
    box-shadow: 2px 2px 3px #999;
    animation: wiggle 2.5s infinite;
}

@keyframes wiggle {
    0% { transform: rotate(0deg); }
    80% { transform: rotate(0deg); }
    85% { transform: rotate(5deg); }
    95% { transform: rotate(-5deg); }
    100% { transform: rotate(0deg); }
}

